<template>
  <!-- 笔记列表页面 -->
  <div class="notesBox">
    <p class="title">学习笔记</p>
    <!-- 布局盒子 -->
    <div class="layout">
      <!-- 滚动盒子 -->
      <scrollbar-container
        ref="scrollbarRef"
        height="100%"
        class="scroll-container"
      >
        <ul class="notesList">
          <!-- item.name 是组件对象需要在获取里面name -->
          <li
            v-for="item in article"
            :key="item.id"
            @click="intoDetails(item.name.name, item.date, item.type)"
          >
            <p>{{ item.describe }}</p>
            <p>记录时间：{{ item.date }}</p>
          </li>
        </ul>
      </scrollbar-container>
    </div>
  </div>
</template>
<script>
// 导入文章列表
import { components } from "../article/index";
export default {
  data() {
    return {
      article: components,
    };
  },
  mounted() {
    // *注：demo更新需要更新容器滚动条
    this.$nextTick(() => {
      this.$refs.scrollbarRef.updateScroll();
    });
  },
  methods: {
    // 点击文章，传递name进入详情页面
    intoDetails(name, date, tag) {
      // url中显示参数跳转
      this.$router.push({
        name: "details",
        query: { article: name, date: date, tag: tag },
      });
    },
  },
};
</script>
<style lang="less" scoped>
ul,
li,
p {
  padding: 0;
  margin: 0;
  list-style: none;
}
.notesBox {
  position: relative;
  height: 100%;
  .title {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 0 40px;
    box-sizing: border-box;
    line-height: 50px;
    font-style: italic;
    font-size: 20px;
    background: rgb(226, 232, 255);
  }
  .layout {
    height: 100%;
    padding-top: 50px;
    box-sizing: border-box;
    .notesList {
      padding: 0px 40px;
      height: 100%;
      li {
        padding: 10px;
        transition: 0.3s;
        border-bottom: 1px solid #ddd;
        p:nth-child(1) {
          font-size: 16px;
          line-height: 40px;
        }
        p:nth-child(2) {
          font-size: 12px;
          color: rgb(146, 146, 146);
        }
        &:hover {
          cursor: pointer;
          color: rgb(0, 60, 255);
        }
      }
    }
  }
}
// 列表页标题
.min .layout .notesList,
.min .title {
  padding: 0px 5px;
  line-height: 30px;
  font-size: 18px;
}
.min .layout {
  padding-top: 30px;
}
// 例表页内容
.min .layout .notesList li {
  padding: 5px 0px;
}
.min .layout .notesList li p:nth-child(1) {
  line-height: 24px;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis; /* 加省略号 */
  white-space: nowrap; /* 强制不换行 */
}
.min .layout .notesList li p:nth-child(2) {
  line-height: 18px;
  font-size: 12px;
}
</style>
